import DrawerComponents from '@/components/coverComponents/drawerComponents.vue';
import TempateForm from "@/components/optionsForm/templateForm.vue";
import { ref, toRefs, defineComponent } from 'vue';
export default defineComponent({
  components: {
    DrawerComponents,
    TempateForm
  },
  props: {
    value: Boolean,
    modelType: {
      type: Number,
      default: 1
    },
    modelInfo: Object
  },
  emits: ['update:modelValue', 'submitSuccss'],

  setup(props, {
    emit
  }) {
    let {
      modelType
    } = toRefs(props);
    let showDrawer = ref(false),
        formDOM = ref();

    const confirmSubmit = () => formDOM.value.validate(result => emit('submitSuccss', result, modelType.value));

    const closedModel = () => {
      formDOM.value?.resetFields();
      emit('update:modelValue', false);
    };

    const formOptions = {
      inline: false,
      rules: {
        name: {
          required: true
        },
        key: {
          required: true
        }
      },
      items: [{
        controlType: "input",
        keyName: "name",
        label: "角色名称"
      }, {
        controlType: "input",
        keyName: "key",
        label: "角色标识"
      }, {
        controlType: "switch",
        keyName: "disabled",
        label: "是否启用"
      }]
    };
    return {
      showDrawer,
      formOptions,
      formDOM,
      confirmSubmit,
      closedModel
    };
  }

});