import DrawerComponents from '@/components/coverComponents/drawerComponents.vue';
import AddAndEditRoleModal from './addAndEditRoleModal.vue';
import { ref, defineComponent } from 'vue';
import { mapStoreState } from '@/utils/storeFn';
import { ElMessage, ElMessageBox } from 'element-plus';
export default defineComponent({
  components: {
    DrawerComponents,
    AddAndEditRoleModal
  },
  props: {
    value: Boolean
  },
  emits: ['update:modelValue'],

  setup(props, {
    emit
  }) {
    let showDrawer = ref(false),
        modelType = ref(1),
        // 1 add 2 edit
    modelInfo = ref({}),
        roleList = ref([]);
    let stateList = ['role/roleList'];
    let stateData = mapStoreState(stateList);

    const openRoleModal = (type, data) => {
      modelType.value = type;
      type == 2 && (modelInfo = data);
      showDrawer.value = !showDrawer.value;
    };

    const confirmSubmit = (result, type) => {
      //
      console.log(result, type, '结果类型');
    };

    const deleteRole = key => {
      ElMessageBox.confirm('确定删除该角色吗？', '提示', {
        type: 'warning'
      }).then(() => {
        ElMessage({
          type: 'success',
          message: 'Delete completed'
        });
      }).catch(() => ({}));
    };

    return {
      showDrawer,
      modelType,
      modelInfo,
      roleList: stateData.roleList,
      ...stateData,
      confirmSubmit,
      openRoleModal,
      deleteRole
    };
  }

});