import DrawerComponents from '@/components/coverComponents/drawerComponents.vue';
import TempateForm from "@/components/optionsForm/templateForm.vue";
import { ref, toRefs, defineComponent } from 'vue';
export default defineComponent({
  components: {
    DrawerComponents,
    TempateForm
  },
  props: {
    value: Boolean,
    modelType: {
      type: Number,
      default: 1
    },
    modelInfo: Object
  },
  emits: ['update:modelValue', 'submitSuccss'],

  setup(props, {
    emit
  }) {
    let {
      modelType
    } = toRefs(props);
    let showDrawer = ref(false),
        formDOM = ref();

    const confirmSubmit = () => {
      formDOM.value.validate(result => {
        emit('submitSuccss', result, modelType.value);
      });
    };

    const closedModel = () => {
      formDOM.value?.resetFields();
      emit('update:modelValue', false);
    };

    const formOptions = {
      inline: false,
      rules: {
        title: {
          required: true
        },
        name: {
          required: true
        },
        menuType: {
          required: true
        }
      },
      items: [{
        controlType: "input",
        keyName: "title",
        label: "资源名称"
      }, {
        controlType: "input",
        keyName: "name",
        label: "资源标识"
      },
      /* {
          controlType: "select",
          slot: "authName",
          keyName: "authName",
          label: "关联权限",
          multiple: true,
          filterable: true,
          pleaceholader: "支持搜素",
          options: [
              { label: "userQuery", value: "userQuery" }
          ]
      } */
      {
        controlType: "radio",
        keyName: "menuType",
        label: "资源类型",
        options: [{
          label: "菜单",
          value: 1
        }, {
          label: "按钮",
          value: 2
        }]
      }, {
        controlType: "select",
        keyName: "authName",
        label: "关联权限",
        multiple: true,
        filterable: true,
        pleaceholader: "支持搜素",
        options: [{
          label: "userQuery",
          value: "userQuery"
        }, {
          label: "userDelete",
          value: "userDelete"
        }]
      }]
    };
    return {
      showDrawer,
      formOptions,
      formDOM,
      confirmSubmit,
      closedModel
    };
  }

});