import DrawerComponents from '@/components/coverComponents/drawerComponents.vue';
import RoleAuthBeUserModelVue from './roleAuthBeUserModel.vue';
import { ref, reactive, defineComponent, watch, toRefs } from 'vue';
import { mapStoreState } from '@/utils/storeFn'; // import type { TabsPaneContext } from 'element-plus'

import { ArrowRight, Search } from '@element-plus/icons-vue';
export default defineComponent({
  components: {
    DrawerComponents,
    ArrowRight,
    RoleAuthBeUserModelVue
  },
  props: {
    value: Boolean,
    modelType: {
      type: Number,
      default: 1
    },
    modelInfo: Object
  },
  emits: ['update:modelValue', 'submitSuccss'],

  setup(props, {
    emit
  }) {
    let activeName = ref('people'),
        activeTreeKey = ref([]),
        searchValue = ref(""),
        authority_tree = ref(),
        currentSideList = ref([{
      name: "用户1"
    }, {
      name: "用户2"
    }, {
      name: "用户3"
    }]),
        currentSeleteName = ref(''),
        defaultProps = {
      children: 'children',
      label: 'title'
    },
        pageData = reactive({
      pageNo: 1,
      limit: 10,
      pageSize: 10,
      total: 0
    }); // 用户

    let userData = reactive({
      userList: []
    }); // 角色

    let roleData = reactive({
      // activeRoleTabName: 'relationAuthority',
      activeRoleTabName: 'authUser',
      roleList: []
    }); // 

    let stateList = ['resources/menuResources'];
    let stateData = mapStoreState(stateList); // 获取相关的权限

    const getRelevantAuthList = id => {
      let requestType = activeName.value === 'people' ? 1 : 2;
      console.log(id, requestType, '类型');
      currentSeleteName.value = id;
    }; // 提交搜索


    const searchContent = () => {
      console.log(activeName.value, searchValue, '当前');
    }; // 分页数据更改


    const changePagination = () => {
      let {
        pageNo,
        pageSize
      } = pageData;
      console.log(pageNo, pageSize, activeName.value, '页面');
    }; // 授权保存


    const confirmSubmit = () => {
      // 
      console.log(authority_tree.value && authority_tree.value.getCheckedKeys(), '获取选中节点');
    }; // 顶部tabs标签页切换触发


    watch(activeName, newValue => {
      console.log(newValue, '新数据');
      pageData.pageNo = 1; // 请求账户、角色列表
      // 请求默认账户、角色的权限列表
    }, {
      immediate: true
    }); // 左侧列表变化触发

    watch(currentSideList, () => {
      // 发起权限请求
      if (currentSideList.value.length > 0) {
        let selectId = currentSideList.value[0].name;
        currentSeleteName.value = selectId;
        getRelevantAuthList(selectId);
      }
    }, {
      immediate: true
    });
    return {
      activeName,
      // activeRoleTabName,
      activeTreeKey,
      currentSideList,
      defaultProps,
      authority_tree,
      pageData,
      searchValue,
      currentSeleteName,
      menuResources: stateData.menuResources,
      ...stateData,
      ...toRefs(roleData),
      ...toRefs(userData),
      Search,
      confirmSubmit,
      getRelevantAuthList,
      searchContent,
      changePagination
    };
  }

});