import DrawerComponents from '@/components/coverComponents/drawerComponents.vue';
import TempateForm from "@/components/optionsForm/templateForm.vue";
import { ref, defineComponent } from 'vue';
import { editAppFormOptions } from './options.js';
export default defineComponent({
  components: {
    DrawerComponents,
    TempateForm
  },
  props: {
    value: Boolean,
    modelInfo: {
      reqired: true,
      type: Object
    },
    modelType: {
      type: Number,
      default: 1
    }
  },
  emits: ['update:modelValue'],

  setup(props, {
    emit
  }) {
    let showDrawer = ref(false),
        formDOM = ref();
    const {
      rules,
      items: optionsItem,
      ...rest
    } = editAppFormOptions;
    const formOptions = { ...rest,
      rules: { ...rules
      },
      items: [...optionsItem]
    };

    const confirmSubmit = () => {
      formDOM.value?.validate(result => {
        console.log(result, "提交表单"); // emit('update:modelValue', false)
      });
    };

    const closedModel = () => {
      formDOM.value?.resetFields();
      emit('update:modelValue', false);
    };

    return {
      showDrawer,
      formOptions,
      formDOM,
      confirmSubmit,
      closedModel
    };
  }

});