import "core-js/modules/es.error.cause.js";
import DrawerComponents from '@/components/coverComponents/drawerComponents.vue';
import TempateForm from "@/components/optionsForm/templateForm.vue";
import { ref, defineComponent } from 'vue';
export default defineComponent({
  components: {
    DrawerComponents,
    TempateForm
  },
  props: {
    value: Boolean
  },
  emits: ['update:modelValue', 'submitSuccss'],

  setup(props, {
    emit
  }) {
    let showDrawer = ref(false),
        formData = ref({}),
        formDOM = ref();

    const confirmSubmit = () => {
      formDOM.value.validate(result => {
        let res = new Function("return " + result.router.replace(/(component.+)|(import.+)/gm, ""))();
        emit('submitSuccss', res);
        emit('update:modelValue', false);
      });
    };

    const closedModel = () => {
      formDOM.value?.resetFields();
      emit('update:modelValue', false);
    };

    const formOptions = {
      inline: false,
      rules: {
        router: {
          required: true,
          validator: (rule, value, callback) => {
            try {
              let res = value && new Function("return " + value.replace(/(component.+)|(import.+)/gm, ""))() || "";
              if (Array.isArray(res)) callback();else callback(new Error('请输入JSON数组'));
            } catch {
              callback(new Error('请输入JSON字符串'));
            }
          }
        }
      },
      items: [{
        controlType: "input",
        type: 'textarea',
        rows: "10",
        keyName: "router",
        label: "路由集合"
      }]
    };
    return {
      showDrawer,
      formData,
      formOptions,
      formDOM,
      confirmSubmit,
      closedModel
    };
  }

});