import DrawerComponents from '@/components/coverComponents/drawerComponents.vue';
import AddAndEditResourcesModal from './addAndEditResourcesModal.vue';
import AddParentRouterModal from './addParentRouterModal.vue';
import { ref, defineComponent, watch, toRaw } from 'vue';
import { mapStoreState } from '@/utils/storeFn';
import { deepClone } from '@/utils/toolFn';
import { AddAndEdit } from '@/declareTs/model'; // interface TreeNode {
// }

export default defineComponent({
  components: {
    DrawerComponents,
    AddAndEditResourcesModal,
    AddParentRouterModal
  },
  props: {
    value: Boolean,
    modelInfo: {
      reqired: true,
      type: Object
    }
  },
  emits: ['update:modelValue'],

  setup(props, {
    emit
  }) {
    let showEditDrawer = ref(false),
        editModelInfo = ref({
      title: "",
      name: ""
    }),
        showParentDrawer = ref(false),
        menuMergeResources = ref([]),
        treeNodeObj = ref({}),
        editType = ref(1); // 1 add 2 edit

    let stateList = ['resources/menuResources'];
    let stateData = mapStoreState(stateList);
    let {
      menuResources
    } = stateData;
    watch(menuResources, newValue => menuMergeResources.value = deepClone(newValue), {
      immediate: true
    });
    watch(menuMergeResources, newValue => {
      // 递归修改节点
      const recursionTreeNodeObj = (list, result = {}, parentIds = []) => {
        list.forEach((item, index) => {
          item.parentIds = parentIds.concat(index);

          if (!item.timeKey || !result[item.timeKey]) {
            let timeKey = `${Date.now()}_${item.name}`;
            item.timeKey = timeKey;
            result[timeKey] = item;
          }

          if (item.children && item.children.length > 0) recursionTreeNodeObj(item.children, result, item.parentIds.concat());
        });
        return result;
      };

      treeNodeObj.value = recursionTreeNodeObj(newValue);
    }, {
      immediate: true
    });
    /* *********************** 树形控件逻辑 */

    const isShowTreeMenu = ref(false),
          currentSelectNode = ref(),
          tree_node_menu = ref(),
          defaultProps = {
      children: 'children',
      label: 'title'
    }; // 树节点右键菜单

    const openNodeContextmenu = (event, node) => {
      let {
        clientX,
        clientY
      } = event;
      tree_node_menu.value.style.left = `${clientX + 20}px`;
      tree_node_menu.value.style.top = `${clientY + 10}px`;
      isShowTreeMenu.value = true;
      currentSelectNode.value = node;
      document.addEventListener("click", hideNodeMenu, {
        once: true
      });
    };

    const hideNodeMenu = () => isShowTreeMenu.value && (isShowTreeMenu.value = false); // 打开资源修改


    const openResourceDrawer = (type = AddAndEdit, row) => {
      editType.value = type;
      !row && (currentSelectNode.value = null);
      showEditDrawer.value = !showEditDrawer.value;
      setTimeout(() => {
        editModelInfo.value = type === 2 && row ? deepClone(row) : {};
      }, 0);
    }; // 单个节点新增/修改


    const submitNodeEdit = (data, type = AddAndEdit) => {
      let currentNode = currentSelectNode.value ? treeNodeObj.value[currentSelectNode.value.timeKey] : null;

      if (type === 1) {
        let timeKey = `${Date.now()}_${data.name}`,
            addData = { ...data,
          timeKey
        };

        if (currentNode && currentSelectNode) {
          !currentNode.children && (currentSelectNode.value.children = []);
          currentNode.children.push(addData);
        } else menuMergeResources.value.push(addData);

        treeNodeObj.value[timeKey] = addData;
      }

      if (type === 2) {
        currentSelectNode.value = data;
        Object.keys(data).forEach(key => key != 'children' && (currentNode[key] = data[key]));
      }

      showEditDrawer.value = false;
    }; // 导入父级路由


    const importRouter = list => {
      // 递归返回路由列表
      const recursionRouterData = (list, result = []) => {
        list.forEach(item => {
          let currentRoute = {};

          if (item.name && item.meta && item.meta.title) {
            currentRoute['name'] = item.name;
            currentRoute['title'] = item.meta.title;
            if (item.children && item.children.length > 0) currentRoute['children'] = recursionRouterData(item.children, []);
            result.push(currentRoute);
          }
        });
        return result;
      };

      let filterRoutes = recursionRouterData(list);
      menuMergeResources.value = menuMergeResources.value.concat(filterRoutes);
    }; // 删除权限


    const deleteAuthority = node => {
      const recursionDeleteNode = (nodeList, nodeIds) => {
        let currentList = nodeList;

        if (nodeIds.length > 1) {
          let index = nodeIds.shift();
          currentList = nodeList[index].children;
          recursionDeleteNode(currentList, nodeIds);
        } else if (nodeIds.length === 1) currentList.splice(nodeIds[0], 1);
      };

      recursionDeleteNode(menuMergeResources.value, node.parentIds);
    };
    /* **************** */
    // 保存树状图


    const confirmSubmit = () => {
      let treeData = deepClone(toRaw(menuMergeResources.value));

      const recursionFilterList = (authList, result = []) => {
        if (authList.length === 0) return result;
        let item = authList.shift();

        if (item) {
          if (item.children && item.children.length > 0) item.children = recursionFilterList(item.children);
          let target = ['title', 'name', 'children'].reduce((res, key) => {
            if (item && item[key] !== undefined) res[key] = typeof item[key] === 'object' ? deepClone(item[key]) : item[key];
            return res;
          }, {});
          result.push(target);
          result = result.concat(recursionFilterList(authList));
        }

        return result;
      };

      console.log(recursionFilterList(treeData), '新数组');
    };

    const closedModel = () => emit('update:modelValue', false);

    return {
      showEditDrawer,
      showParentDrawer,
      editType,
      defaultProps,
      isShowTreeMenu,
      currentSelectNode,
      tree_node_menu,
      menuMergeResources,
      editModelInfo,
      confirmSubmit,
      closedModel,
      openNodeContextmenu,
      openResourceDrawer,
      hideNodeMenu,
      submitNodeEdit,
      importRouter,
      deleteAuthority
    };
  }

});